/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-landscape {

	.edgtf-woocommerce-page {

		table.cart {

			.edgtf-quantity-buttons {

	            .edgtf-quantity-input,
	            .edgtf-quantity-minus,
	            .edgtf-quantity-plus {
	                font-size: 13px;
	            }

	            .edgtf-quantity-input {
	                width: 64px;
	                height: 54px;
	            }

	            .edgtf-quantity-minus,
	            .edgtf-quantity-plus {
	                width: 40px;
	                height: 33px;
	                line-height: 34px;
	            }
	        }
		}
	}
}

@include ipad-portrait {
	
	.edgtf-woocommerce-page {
		
		table.cart {

			td.actions {
				
				.coupon,
				.button,
				> input {
					display: block !important;
					position: relative !important;
					float: none !important;
					margin: 0;
				}
				
				.coupon {
					margin: 0 0 10px;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.edgtf-woocommerce-page {
		
		table.cart {
			
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {
						
						&.product-thumbnail {
							width: auto;
							padding-right: 0;
							
							img {
								display: none;
							}
						}
					}
				}
			}

			.edgtf-quantity-buttons {

	            .edgtf-quantity-input {
	                width: 54px;
	                height: 44px;
		            margin: 0;
	            }

	            .edgtf-quantity-minus,
	            .edgtf-quantity-plus {
	                display: none;
	            }
	        }
			
			td.actions {
				
				.coupon,
				> input {
					float: none;
					display: block;
				}
			}
		}
	}
}

@include phone-portrait {
	
	.edgtf-woocommerce-page {
		
		table.cart {
			
			thead tr {
				
				th {
					font-size: 16px;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */